body {
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-style: Arial;
  font-size: 30px;
}

.calendar {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 1px;
  width: 706px;
  padding: 10px;
}
.calendar__day {
  box-sizing: border-box;
  background-color: #eee;
  border: 1px solid black;
  height: 100px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
