.calendar--start-mon .calendar__day:first-child {
  margin-left: calc(0 * (100px + 1px));
}
.calendar--start-tue .calendar__day:first-child {
  margin-left: calc(1 * (100px + 1px));
}
.calendar--start-wed .calendar__day:first-child {
  margin-left: calc(2 * (100px + 1px));
}
.calendar--start-thu .calendar__day:first-child {
  margin-left: calc(3 * (100px + 1px));
}
.calendar--start-fri .calendar__day:first-child {
  margin-left: calc(4 * (100px + 1px));
}
.calendar--start-sat .calendar__day:first-child {
  margin-left: calc(5 * (100px + 1px));
}
.calendar--start-sun .calendar__day:first-child {
  margin-left: calc(6 * (100px + 1px));
}
.calendar--month-28 .calendar__day:nth-child(n+29) {
  display: none;
}
.calendar--month-29 .calendar__day:nth-child(n+30) {
  display: none;
}
.calendar--month-30 .calendar__day:nth-child(n+31) {
  display: none;
}
.calendar--month-31 .calendar__day:nth-child(n+32) {
  display: none;
}

.calendar__day:hover {
  cursor: pointer;
  background-color: #FFBFCB;
  transition: transform 0.5s;
  transform: translateY(-20px);
}

